const Routers = [{
        path: '/',
        name: 'main',
        meta: {
            title: 'yalan-mobile'
        },
        component(resolve) {
            require.ensure(['../components/main.vue'], () => {
                resolve(require('../components/main.vue'))
            })
        }
    },
    {
        path: '/login',
        name: 'login',
        meta: {
            title: '登录'
        },
        component(resolve) {
            require.ensure(['../components/login.vue'], () => {
                resolve(require('../components/login.vue'))
            })
        }
    },
    {
        path: '/todayIncome',
        name: 'todayIncome',
        meta: {
            title: '营业额统计'
        },
        component(resolve) {
            require.ensure(['../components/todayIncome/list.vue'], () => {
                resolve(require('../components/todayIncome/list.vue'))
            })
        }
    },
    {
        path: '/todayIncomeDetail',
        name: 'todayIncomeDetail',
        meta: {
            title: '今日收入明细'
        },
        component(resolve) {
            require.ensure(['../components/todayIncome/detail.vue'], () => {
                resolve(require('../components/todayIncome/detail.vue'))
            })
        }
    },
    {
        path: '/wallet',
        name: 'wallet',
        meta: {
            title: '钱包'
        },
        component(resolve) {
            require.ensure(['../components/wallet/list.vue'], () => {
                resolve(require('../components/wallet/list.vue'))
            })
        }
    },
    {
        path: '/withdrawals',
        name: 'withdrawals',
        meta: {
            title: '提现记录'
        },
        component(resolve) {
            require.ensure(['../components/wallet/withdrawals.vue'], () => {
                resolve(require('../components/wallet/withdrawals.vue'))
            })
        }
    },
    {
        path: '/getMoney',
        name: 'getMoney',
        meta: {
            title: '我的钱包'
        },
        component(resolve) {
            require.ensure(['../components/wallet/getMoney.vue'], () => {
                resolve(require('../components/wallet/getMoney.vue'))
            })
        }
    },
    {
        path: '/bankCard',
        name: 'bankCard',
        meta: {
            title: '银行卡'
        },
        component(resolve) {
            require.ensure(['../components/wallet/bankCard.vue'], () => {
                resolve(require('../components/wallet/bankCard.vue'))
            })
        }
    },
    {
        path: '/history',
        name: 'history',
        meta: {
            title: '设备启动记录'
        },
        component(resolve) {
            require.ensure(['../components/history/list.vue'], () => {
                resolve(require('../components/history/list.vue'))
            })
        }
    },
    {
        path: '/device',
        name: 'device',
        meta: {
            title: '设备列表'
        },
        component(resolve) {
            require.ensure(['../components/device/list.vue'], () => {
                resolve(require('../components/device/list.vue'))
            })
        }
    },
    {
        path: '/order',
        name: 'order',
        meta: {
            title: '订单管理'
        },
        component(resolve) {
            require.ensure(['../components/order/list.vue'], () => {
                resolve(require('../components/order/list.vue'))
            })
        }
    },
    {
        path: '/person',
        name: 'person',
        meta: {
            title: '订单管理'
        },
        component(resolve) {
            require.ensure(['../components/person/list.vue'], () => {
                resolve(require('../components/person/list.vue'))
            })
        }
    },
    {
        path: '/choose',
        name: 'choose',
        meta: {
            title: '选择开通'
        },
        component(resolve) {
            require.ensure(['../components/person/choose.vue'], () => {
                resolve(require('../components/person/choose.vue'))
            })
        }
    },
    {
        path: '/one',
        name: 'one',
        meta: {
            title: '添加人员'
        },
        component(resolve) {
            require.ensure(['../components/person/one.vue'], () => {
                resolve(require('../components/person/one.vue'))
            })
        }
    },
    {
        path: '/shop',
        name: 'shop',
        meta: {
            title: '开通店铺'
        },
        component(resolve) {
            require.ensure(['../components/person/shop.vue'], () => {
                resolve(require('../components/person/shop.vue'))
            })
        }
    },
    {
        path: '/addCard',
        name: 'addCard',
        meta: {
            title: '新增银行卡'
        },
        component(resolve) {
            require.ensure(['../components/wallet/addCard.vue'], () => {
                resolve(require('../components/wallet/addCard.vue'))
            })
        }
    },
    { path: '*', redirect: '/' },
]

export default Routers;


