import Axios from 'axios'
import Config from '../config/axios.config.js'
import App from '../main.js'
import Future from "./future.js";
// 数据请求之前
Axios.interceptors.request.use((config) => {
    let Authorization = window.localStorage.getItem('agency_class')
    if (Authorization !== undefined && Authorization !== 'undefined' && Authorization !== '' && Authorization !== null) {
        config.headers.Authorization = "Bearer  " + Authorization;
    } else {
        console.info('agency_class not found');
    }

    return config;
});
// 提交成功并返回数据时
Axios.interceptors.response.use(function (response) {
    return response;
}, function (error) {
    console.log(error.response);
    // App.$dialog.alert({ mes: '服务错误' });
    App.$dialog.loading.close();
    switch (error.response.status) {
        case 400:
            App.$dialog.alert({ mes: error.response.data.err_msg });
            Promise.reject(error.response.data.err_msg);
            break;
        case 401:
            window.localStorage.removeItem("agency_class");
            window.localStorage.user_status = 'outlogin';
            Promise.reject("请重新登录");
            App.$router.push('/login');
            break;
        case 403:
            App.$dialog.alert({ mes: "没有操作权限" });
            Promise.reject("没有操作权限");
            break;
        case 500:
            App.$dialog.alert({ mes: "服务器错误" });
            Promise.reject("服务器错误");
            break;
        default:
            Promise.resolve(error.response.data.data);
            break;
    }
});

class Ajax {
    static ResponseHandle(F, res, showLoadingBoolean) {
        // 解决loading多次显示的问题
        if (showLoadingBoolean || showLoadingBoolean == undefined) {
            App.$dialog.loading.close();
        }
        switch (res.data.errcode) {
            case 400:
                App.$dialog.alert({ mes: res.data.message });
                F.reject(res.data.message);
                break;
            case 403:
                window.localStorage.removeItem("agency_class");
                window.localStorage.user_status = 'outlogin';
                App.$router.push('/login');
                break;
            case 500:
                App.$dialog.alert({ mes: res.data.message });
                F.reject(res.data.message);
                break;
            default:
                F.resolve(res.data.data);
                break;
        }
    }

    static chooseLoading(url, showLoadingBoolean) {
        if (!showLoadingBoolean || showLoadingBoolean == undefined) {
            var pattern = /user\/bank\/branch/;
            if (!pattern.test(url)) {
                App.$dialog.loading.open('很快加载好了'); //loading调用
            }
        }
    }
    static Post(url, data, showLoadingBoolean) {
        Ajax.chooseLoading(url, showLoadingBoolean);
        var F = new Future();
        Axios.post(url, data, Config)
            .then((res) => {
                Ajax.ResponseHandle(F, res, showLoadingBoolean);
            })
            .catch((e) => {
                F.reject(e);
            });
        return F;
    }
    static Del(url, data, showLoadingBoolean) {
        Ajax.chooseLoading(url, showLoadingBoolean);
        var F = new Future();
        let newUrl = Config.baseURL + url + "/" + data.id;
        console.log(newUrl);
        Axios.delete(newUrl, "", Config)
            .then((res) => {
                console.log(res);
                Ajax.ResponseHandle(F, res, showLoadingBoolean);
            })
            .catch((e) => {
                F.reject(e);
            });
        return F;
    }
    static PostExel(url, data, Config, showLoadingBoolean) {
        Ajax.chooseLoading(url, showLoadingBoolean);
        var F = new Future();
        Axios.post(url, data, Config)
            .then((res) => {
                Ajax.ResponseHandle(F, res, showLoadingBoolean);
            })
            .catch((e) => {
                F.reject(e);
            });
        return F;
    }
    static Get(url, data, showLoadingBoolean) {
        Ajax.chooseLoading(url, showLoadingBoolean);
        if (data) {
            let str = "";
            for (let i in data) {
                if (data[i] != "") {
                    str += "&" + i + "=" + data[i];
                }
            }
            if (str) {
                url = url + "?" + str.slice(1);
            }

        }
        var F = new Future();
        Axios.get(url, Config)
            .then((res) => {
                Ajax.ResponseHandle(F, res, showLoadingBoolean);
            })
            .catch((e) => {
                F.reject(e);
            });
        return F;
    }
}

export default Ajax;


