const StoreObj = {
    state: {
        qrcodelist: "",
        qrcodepage: "",
        telephone: "",
    },
    mutations: {
        qrcodeList(state, parmas) {
            state.qrcodelist = parmas;
        },
        qrcodePage(state, parmas) {
            state.qrcodepage = parmas;
        },
        telePhone(state, parmas) {
            state.telephone = parmas;
        },
    }
}
export default StoreObj;


