const auth = {
    loggedIn() {
        var token = window.localStorage.getItem('agency_class');
        var user_status = window.localStorage.user_status;
        if (!token || token === 'undefined') {
            return false;
        }
        if (user_status != 'platfrom_join_login') {
            return false;
        }
        return true;
    },
    setLogged(token) {
        console.log(token);
        window.localStorage.setItem('agency_class', token);
        window.localStorage.user_status = 'platfrom_join_login';
    }
}
export default auth;


