import jsBase64 from 'js-base64'
export default {
    setTitle(t) {
        document.title = t;
        var i = document.createElement('iframe');
        i.style.display = 'none';
        i.onload = function() {
            setTimeout(function() {
                i.remove();
            }, 9)
        }
        document.body.appendChild(i);
    },
    quicksort(arr, left, right) {
        if (left < right) {
            var key = arr[left]; //基准数
            var first = left; //开始位置
            var last = right; //结束位置

            while (first < last) {
                while (first < last && arr[last] >= key) {
                    last--;
                }

                arr[first] = arr[last];

                while (first < last && arr[first] <= key) {
                    first++;
                }

                arr[last] = arr[first];
            }
            arr[first] = key;
            this.quicksort(arr, left, first - 1);
            this.quicksort(arr, first + 1, right);
        }
    },
    // 深拷贝对象或者数组
    deepCopy(o) {
        if (o instanceof Array) {
            var n = [];
            for (var i = 0; i < o.length; ++i) {
                n[i] = this.deepCopy(o[i]);
            }
            return n;

        } else if (o instanceof Object) {
            var n = {}
            for (var i in o) {
                n[i] = this.deepCopy(o[i]);
            }
            return n;
        } else {
            return o;
        }
    },
    GetQueryString(queryString, name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = queryString.match(reg);
        if (r != null) return unescape(r[2]);
        return null;
    },
    oauthUrlEncoding(uid) {
        let url = "http://mobile.yl.gzab.net/";
        let serverUrl = "https://yl.gzab.net/" + "web/wechat/oauth?url=" + url + "&uid=" +uid;
        console.log(serverUrl); //consumer.local.com test2.gzmypay.com
        window.location.href = serverUrl;
    },
    stampTime(str) {
        var timestamp2 = Date.parse(new Date(str));
        return timestamp2 = timestamp2 / 1000;
        //2014-07-10 10:21:12的时间戳为：1404958872 
    }
}


